import React, { createContext, useContext, useEffect, useMemo, useState } from 'react';

const STORAGE_KEY = 'appSettings:v1';

export const defaultSettings = {
  theme: {
    mode: 'system', // 'system' | 'light' | 'dark'
  },
  ui: {
    pxPerMin: 1.2,
    dayMinW: 240,
    hoursColW: 72,
    colGap: 8,
    colPad: 6,
    slotHoverMinH: 128,
    monthCellH: 120,     // <- nouveau (optionnel)
    monthCellPad: 6,     // <- nouveau (optionnel)
  },
  schedule: {
    days: 5,
    timeStart: '08:00',
    timeEnd: '19:00',
    showNowLine: true,
  },
  events: {
    days: 7,
  },
  ab: {
    // base Monday/date et type sont d√©j√† g√©r√©s dans Schedule via localStorage
  },
};

export const settingsSchema = {
  theme: {
    mode: { type: 'enum', options: ['system', 'light', 'dark'], label: 'Th√®me' },
  },
  ui: {
    pxPerMin: { type: 'number', min: 0.8, max: 3, step: 0.1, label: 'Densit√© verticale (px/min)' },
    dayMinW: { type: 'number', min: 160, max: 420, step: 10, label: 'Largeur min d‚Äôun jour (px)' },
    hoursColW: { type: 'number', min: 56, max: 120, step: 2, label: 'Largeur colonne heures (px)' },
    colGap: { type: 'number', min: 0, max: 24, step: 1, label: 'Espace entre colonnes (px)' },
    colPad: { type: 'number', min: 0, max: 16, step: 1, label: 'Padding interne colonne (px)' },
  },
  schedule: {
    timeStart: { type: 'time', label: 'Heure d√©but' },
    timeEnd: { type: 'time', label: 'Heure fin' },
    showNowLine: { type: 'boolean', label: 'Afficher la ligne ‚Äúmaintenant‚Äù' },
  },
  events: {
    // pr√™t pour plus tard
  },
};

function deepMerge(base, patch) {
  const out = Array.isArray(base) ? [...base] : { ...base };
  for (const k of Object.keys(patch || {})) {
    const v = patch[k];
    if (v && typeof v === 'object' && !Array.isArray(v)) {
      out[k] = deepMerge(base?.[k] || {}, v);
    } else {
      out[k] = v;
    }
  }
  return out;
}

function loadSettings() {
  try {
    const raw = localStorage.getItem(STORAGE_KEY);
    if (!raw) return defaultSettings;
    const parsed = JSON.parse(raw);
    return deepMerge(defaultSettings, parsed);
  } catch {
    return defaultSettings;
  }
}

function saveSettings(s) {
  try {
    localStorage.setItem(STORAGE_KEY, JSON.stringify(s));
  } catch {}
}

const SettingsContext = createContext({
  settings: defaultSettings,
  updateSettings: () => {},
});

export function SettingsProvider({ children }) {
  const [settings, setSettings] = useState(loadSettings);

  const updateSettings = (patch) => {
    setSettings((prev) => {
      const next = deepMerge(prev, patch);
      saveSettings(next);
      return next;
    });
  };

  // Th√®me (optionnel): pose un data-theme pour usage futur en CSS
  useEffect(() => {
    const root = document.documentElement;
    const { mode } = settings.theme || {};
    if (mode === 'light' || mode === 'dark') {
      root.setAttribute('data-theme', mode);
    } else {
      root.removeAttribute('data-theme'); // laisse prefers-color-scheme
    }
  }, [settings.theme?.mode]);

  const value = useMemo(() => ({ settings, updateSettings }), [settings]);
  return <SettingsContext.Provider value={value}>{children}</SettingsContext.Provider>;
}

export function useSettings() {
  return useContext(SettingsContext);
}

// Utilitaires temps
export function timeToMin(t) {
  if (!t || typeof t !== 'string') return undefined;
  const [h, m] = t.split(':').map(Number);
  if (Number.isNaN(h) || Number.isNaN(m)) return undefined;
  return h * 60 + m;
}

// Variables CSS pour l‚ÄôUI (√† poser sur un wrapper via style={cssVars(settings)})
export function cssVars(s) {
  const ui = s?.ui || {};
  const toNum = (v, d) => (typeof v === 'number' && !Number.isNaN(v) ? v : d);

  const pxPerMin = toNum(ui.pxPerMin, 1.2);
  const hoursColW = toNum(ui.hoursColW, 72);
  const dayMinW = toNum(ui.dayMinW, 240);
  const colGap = toNum(ui.colGap, 8);
  const colPad = toNum(ui.colPad, 6);
  const slotHoverMinH = toNum(ui.slotHoverMinH, 128);
  const monthCellH = toNum(ui.monthCellH, 120);
  const monthCellPad = toNum(ui.monthCellPad, 6);

  return {
    '--px-per-min': String(pxPerMin),
    '--hours-col-w': `${hoursColW}px`,
    '--day-min-w': `${dayMinW}px`,
    '--col-gap': `${colGap}px`,
    '--col-pad': `${colPad}px`,
    '--slot-hover-min-h': `${slotHoverMinH}px`,
    '--month-cell-h': `${monthCellH}px`,
    '--month-cell-pad': `${monthCellPad}px`,
  };
}